import React, { useEffect } from "react";
import Yohan from "../images/Yohan_icon.svg";

function Header(props) {
  const resizeSection = () => {
    const header = document.querySelector(".header");

    if (window.matchMedia("screen and (min-width: 875px)").matches) {
      header.style.height =
        //window.visualViewport.height -
        window.innerHeight -
        document.querySelector(".navbar").offsetHeight +
        "px";
    } else {
      header.style.height = window.innerHeight + "px";
      //window.visualViewport.height + "px";
    }
  };

  useEffect(() => {
    const header = document.querySelector(".header-section-center");

    header.addEventListener("mouseenter", (e) => {
      e.preventDefault();
      document.querySelectorAll(".header-line").forEach((line) => {
        const newLine = line.cloneNode();
        line.parentNode.replaceChild(newLine, line);
      });
    });
    resizeSection();
    setTimeout(() => {
      document.querySelector(".header-introduction").classList.add("show");
      document.querySelector(".header-desc").classList.add("show");
    }, 3000);
  }, []);

  return (
    <section className="header">
      <div className="header-icons">
        <img className="website-icon" src={Yohan} alt="Yohan" />
        <img
          className="navbar-menu-icon"
          src={require(`../images/bars.png`)}
          alt="Hamburger Icon"
          onClick={props.clickHandler}
        />
      </div>
      <div className="header-section-center">
        <div className="header-introduction">
          <h1>
            Hey! I'm <strong>Yohan</strong>
          </h1>
        </div>
        <h3 className="header-desc">I'm a software developer.</h3>
        <span className="header-line top"></span>
        <span className="header-line down"></span>
        <span className="header-line left"></span>
        <span className="header-line right"></span>
      </div>
      <a href="#about" className="down-icon">
        <img
          src={require(`../images/down_chevron.png`)}
          alt="Down Chevron Icon"
        />
      </a>
    </section>
  );
}

export default Header;
