import React, { useEffect, useState } from "react";
import Yohan from "../images/Yohan_icon.svg";

function Navbar(props) {
  let navbar;
  let navHeight;

  // useEffect(() => {
  //   navbar = document.querySelector(".navbar");
  //   navHeight = navbar.getBoundingClientRect().height;

  //   const homeObserver = new IntersectionObserver(stickyNav, {
  //     root: null,
  //     threshold: 0,
  //     rootMargin: "-10px",
  //   }).observe(document.querySelector(".hero"));

  //   document.querySelector(
  //     ".hero"
  //   ).style.transform = `translateY(${-navHeight})`;
  // });

  ////////////////////////////////////////////////////////////////
  // Navbar
  ////////////////////////////////////////////////////////////////

  const stickyNav = (entries) => {
    const [entry] = entries;

    if (!entry.isIntersecting) {
      navbar.classList.add("sticky");
    } else navbar.classList.remove("sticky");
  };

  return (
    <nav className={`navbar ${props.isOpen ? "open" : ""}`}>
      <img
        className="navbar-menu-icon"
        src={require(`../images/close.png`)}
        alt="Close Icon"
        onClick={props.clickHandler}
      />
      <a className="website-icon" href="#">
        <img src={Yohan} alt="Yohan" />
      </a>

      <div className="nav-links">
        <a href="#about" className="nav-link">
          <div className="nav-link__content" onClick={props.clickHandler}>
            <img
              src={require(`../images/account_icon.png`)}
              alt="Contact Icon"
            />
            <span>About</span>
          </div>
        </a>
        <a href="#experience" className="nav-link">
          <div className="nav-link__content" onClick={props.clickHandler}>
            <img
              src={require(`../images/office_bag_icon.png`)}
              alt="Experience Icon"
            />
            <span>Experience</span>
          </div>
        </a>
        <a href="#projects" className="nav-link">
          <div className="nav-link__content" onClick={props.clickHandler}>
            <img
              src={require(`../images/monitor_icon.png`)}
              alt="Projects Icon"
            />
            <span>Project</span>
          </div>
        </a>
        <a href="#contact" className="nav-link">
          <div className="nav-link__content" onClick={props.clickHandler}>
            <img
              src={require(`../images/message_icon.png`)}
              alt="Contact Icon"
            />
            <span>Contact</span>
          </div>
        </a>
      </div>
    </nav>
  );
}

export default Navbar;
