import React from "react";

function Projects() {
  return (
    <section className="section" id="projects">
      <div className="section-title">
        <h2>Projects</h2>
        <div className="underline"></div>
      </div>
      <div className="section-center">
        <div className="project">
          <img src={require(`../images/davinci_code.png`)} alt="" />
          <div className="project__content">
            <h3>DaVinci Code</h3>
            <p>
              DaVinci Code is an online multiplayer card game based on the
              "DaVinci Code" board game I often play with my friends. Since it
              was difficult for us to meet during the lockdown, I decided to
              make an online version of the game. It includes features such as
              an initial lobby and an in-game chat.
            </p>
            <div className="project__tools">
              <div className="tool">Socket.IO</div>
              <div className="tool">Phaser 3</div>
              <div className="tool">Heruko</div>
            </div>
          </div>
        </div>
        <div className="project">
          <img src={require(`../images/coterie.png`)} alt="" />
          <div className="project__content">
            <h3>Coterie</h3>
            <p>
              Coterie is a social networking app for building new communities
              for special interests. This 6-person team project allows users to
              create specialized rooms where they can share, discuss, and
              organize with other users sharing similar interests.
            </p>
            <div className="project__tools">
              <div className="tool">Android Studio</div>
              <div className="tool">Java</div>
              <div className="tool">Firebase</div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Projects;
